<template>
  <!-- Show Winner Template -->
  <section class="hero is-light is-bold">
    <div class="hero-body">
      <div class="container">
        <div class="columns">
          <div class="column">
            <figure class="image">
              <img
                :src="winner.logo"
                class="animated flipInX"
                alt="Winner Logo"
                style="max-height: 300px;"
              />
            </figure>
            <div class="title has-text-centered animated fadeInDown">Winner</div>
          </div>
          <div class="column is-9">
            <article class="media" style="margin-bottom: 19px;">
              <figure class="media-left">
                <p class="image is-64x64">
                  <img :src="winner.img" />
                </p>
              </figure>
              <div class="media-content">
                <p class="animated bounceInDown">
                  <a :href="winner.channelUrl" target="_new">
                    <strong style="font-size: x-large;">{{ winner.name }}</strong>
                  </a>
                </p>
                <small>( {{winner.totalComments+winner.totalReplies}} comments )</small>
              </div>
              <div class="media-right">
                <b-dropdown aria-role="list" position="is-bottom-left">
                  <button class="button is-danger is-small" slot="trigger" slot-scope="{ active }">
                    <b-icon :icon="active ? 'menu-up' : 'menu-down'"></b-icon>
                  </button>
                  <b-dropdown-item @click="generateSVGImg()">
                    <b-icon icon="image"></b-icon>
                    <span style="vertical-align: super;">&nbsp; Download SVG</span>
                  </b-dropdown-item>
                  <b-dropdown-item @click="generateQRCode()">
                    <b-icon icon="qrcode"></b-icon>
                    <span style="vertical-align: super;">&nbsp; Generate QR Code</span>
                  </b-dropdown-item>
                </b-dropdown>
              </div>
            </article>
            <!-- Comments -->
            <div class="content has-text-centered animated fadeIn">
              <table class="table is-bordered is-striped is-narrow">
                <tbody>
                  <tr v-for="item in winner.comments" :key="item.id">
                    <td>
                      <b-tooltip :label="item.crdt" position="is-right">&nbsp;{{ item.cmnt }}</b-tooltip>
                    </td>
                    <th style="width: 81px;">
                      <b-icon icon="thumb-up-outline"></b-icon>
                      <span style="vertical-align: inherit;">&nbsp;{{ item.tlks }}</span>
                    </th>
                  </tr>
                </tbody>
              </table>
            </div>
            <!-- Sharing on Social Media -->
            <nav class="level animated swing">
              <div class="level-item">
                <!-- fb show: quote and url -->
                <social-sharing
                  :url="winner.videoUrl"
                  :title="share.title"
                  :quote="share.quote"
                  :description="share.description"
                  :hashtags="share.hashtags"
                  :twitter-user="share.twitterUser"
                  inline-template
                >
                  <div>
                    <network network="email">
                      <b-icon icon="email" id="sharicon" size="is-large" title="Share on Email"></b-icon>
                    </network>&nbsp;
                    <network network="facebook">
                      <b-icon
                        icon="facebook"
                        id="sharicon"
                        size="is-large"
                        title="Share on Facebook"
                      ></b-icon>
                    </network>&nbsp;
                    <network network="twitter">
                      <b-icon icon="twitter" id="sharicon" size="is-large" title="Share on Twitter"></b-icon>
                    </network>&nbsp;
                    <network network="telegram">
                      <b-icon
                        icon="telegram"
                        id="sharicon"
                        size="is-large"
                        title="Share on Telegram"
                      ></b-icon>
                    </network>&nbsp;
                    <network network="linkedin">
                      <b-icon
                        icon="linkedin"
                        id="sharicon"
                        size="is-large"
                        title="Share on Linkedin"
                      ></b-icon>
                    </network>&nbsp;
                    <network network="whatsapp">
                      <b-icon
                        icon="whatsapp"
                        id="sharicon"
                        size="is-large"
                        title="Share on Whatsapp"
                      ></b-icon>
                    </network>&nbsp;
                  </div>
                </social-sharing>
              </div>
            </nav>
            <p
              class="has-text-centered animated pulse"
              style="letter-spacing: 0.1em;"
            >Share the winner on a social media account.</p>
          </div>
        </div>
      </div>
    </div>
    <!-- Download winner svg image -->
