                <a :xlink:href="svgData.winnerChannelUrl" target="_new">
                  <image
                    :xlink:href="svgData.winnerChannelPhoto"
                    x="0"
                    y="0"
                    width="770.79"
                    height="763.33"
                    transform="matrix(0.1365309668028363 0 0 0.1317321688500722 708.7380754859482 141.18749999999997)"
