    </div>
    <!-- QR Code Model -->
    <b-modal :active.sync="qrData.showModel" :width="420" scroll="keep">
      <div class="card" style="padding: 10px;">
        <div class="card-image has-text-centered">
          <h1 class="title">Scan QR code</h1>
          <p>
            <qrcode :value="qrData.value" :options="{ size: 250 }"></qrcode>
          </p>
          <small>Right click to download QR Code.</small>
        </div>
      </div>
    </b-modal>
  </section>
</template>

<script>
import Vue from "vue";

var SocialSharing = require("vue-social-sharing");
Vue.use(SocialSharing);

var download = require("downloadjs");

import VueQrcode from "@chenfengyuan/vue-qrcode";
Vue.component(VueQrcode.name, VueQrcode);

import winnerLogo from "@/assets/img/winner.svg";
import loader from "@/assets/img/loader.gif";

export default {
  name: "winner",
  props: {
    value: {
      type: Object
    }
  },
  data() {
    return {
      winner: {
        logo: winnerLogo,
        videoTitle: "",
        videoThumbnail: "",
        videoUrl: "",
        id: "",
        name: "",
        img: loader,
        channelUrl: "",
        totalComments: 0,
        totalReplies: 0,
        comments: []
      },
      share: {
        title: "",
        quote: "",
        description: "",
        hashtags: "",
        twitterUser: ""
      },
      // SVG File Data
      svgData: {
        svgFileName: "", // SVG File Name
        videoTitle: "",
        videoUrl: "",
        videoThumbnail: "",
        winnerChannelUrl: "",
        winnerChannelName: "",
        winnerChannelPhoto: "",
        winnerComment: ""
      },
      // QRCode Data
      qrData: {
        showModel: false,
        value: ""
      }
    };
  },
  filters: {
    truncate: function(text, stop, clamp) {
      return text.slice(0, stop) + (stop < text.length ? clamp || "..." : "");
    }
  },
  methods: {
    socialSharing() {
      var data = {
        name: this.winner.name,
        channel: this.winner.channelUrl
      };

      var title =
        "" +
        "Congratulations! {name},\n" +
        "\n" +
        "You are elected the winner of this video.\n" +
        "Your YouTube Channel: {channel}\n" +
        "\n" +
        "Thanks for participating.\n" +
        "";

      title = title.replace(/{[^{}]+}/g, function(key) {
        return data[key.replace(/[{}]+/g, "")] || "";
      });

      this.share.title = title;
      this.share.quote = "";
      this.share.description = "";
      this.share.hashtags = "";
      this.share.twitterUser = "youtube";
    },

    /**
     * Generate Winner SVG Image
     */
    generateSVGImg: function() {
      var that = this;

      this.svgData.videoTitle = this.winner.videoTitle;
      this.svgData.videoUrl = this.winner.videoUrl;
      this.svgData.videoThumbnail = this.winner.videoThumbnail;
      this.svgData.winnerChannelUrl = this.winner.channelUrl;
      this.svgData.winnerChannelName = this.winner.name;
      this.svgData.winnerChannelPhoto = this.winner.img;
      this.svgData.winnerComment = this.winner.comments[0]["cmnt"];
      this.svgData.svgFileName = this.winner.videoTitle;

      // Event tracking in Google Analytics
      that.$ga.event(
        "Download SVG",
        that.winner.videoUrl,
        that.winner.channelUrl
      );

      setTimeout(function() {
        var svg_html = document.getElementById("svg").innerHTML;
        download(
          new Blob([svg_html]),
          that.svgData.svgFileName +
            "__Winner Name__ " +
            that.winner.name +
            ".svg",
          "text/svg"
        );
      }, 1000);
    },

    /**
     * Generate Winner QRCode
     */
    generateQRCode: function() {
      // Event tracking in Google Analytics
      this.$ga.event(
        "Generate QRCode",
        this.winner.videoUrl,
        this.winner.channelUrl
      );

      this.qrData.value =
        "Winner : " +
        this.winner.name +
        " \r\nChannel URL: " +
        this.winner.channelUrl +
        " \r\nGiveaway Video: " +
        this.winner.videoUrl +
        " \r\nVideo Title: " +
        this.winner.videoTitle.substring(0, 20) +
        "";

      // Show QRCode Model
      this.qrData.showModel = true;
    }
  },
  mounted() {
    if (
      this.value !== undefined &&
      this.value !== null &&
      typeof this.value === "object"
    ) {
      this.winner.id = this.value.userId;
      this.winner.channelUrl = this.value.channelUrl;
      this.winner.name = this.value.name;
      this.winner.img = this.value.photo;
      this.winner.totalComments = this.value.totalComment;
      this.winner.comments = this.value.comments;
      this.winner.videoTitle = this.value.videoTitle;
      this.winner.videoUrl = this.value.videoUrl;
      this.winner.videoThumbnail = this.value.videoThumbnail;

      // {@call}
      this.socialSharing();
    }
  }
};
</script>